import React from "react";
import { useEffect } from "react";
import { useControls } from "leva";
import {
    getState,
    isHost,
    onPlayerJoin,
    useMultiplayerState,
    usePlayersList,
  } from "playroomkit";
import { randInt } from "three/src/math/MathUtils";

const GameEngineContext = React.createContext();

const TIME_PHASE_CARDS = 10;
const TIME_PHASE_PLAYER_CHIOCE = 10;
const TIME_PHASE_PLAYER_ACTION = 3;
export const NB_ROUNDS = 10;
const NB_GEMS = 3;
const CARDS_PER_PLAYER = 4;


export const GameEngineProvider = ( {children} ) => {
    // GAME STATE
    const [timer, setTimer] = useMultiplayerState("timer", 0);
    const [round, setRound] = useMultiplayerState("round", 1);
    const [phase, setPhase] = useMultiplayerState("setPhase", "lobby");
    const [playerTurn, setPlayerTurn] = useMultiplayerState("playerTurn", 0);
    const [playerStart, setPlayerStart] = useMultiplayerState("playerStart", 0);
    const [deck, setDeck] = useMultiplayerState("deck", []);
    const [gems, setGems] = useMultiplayerState("gems", NB_GEMS);
    const [actionSucces, setActionSucces] = useMultiplayerState("actionSucces", true);
    const [currentEmoji, setCurrentEmoji] = useMultiplayerState("emoji", []);

    const players = usePlayersList(true)
    players.sort((a,b) => a.id.localeCompare(b.id));

    const gameState = {
        timer,
        round,
        phase,
        playerTurn,
        playerStart,
        players,
        gems,
        deck,
        actionSucces,
        currentEmoji,
        setCurrentEmoji,
    };

    const distributeCards = (nbCards) => {
        const newDeck = [...getState("deck")];
        players.forEach((player) => {
            const cards = player.getState("cards") || [];
            for (let i = 0; i < nbCards; i++) {
                const randomIndex = randInt(0, newDeck.length - 1);
                cards.push(newDeck[randomIndex]);
                newDeck.splice(randomIndex, 1);
            }
            player.setState("cards", cards, true);
            player.setState("selectedCard", 0, true);
            player.setState("playerTarget", -1, true);
        });
        setDeck(newDeck, true);
    }

    const startGame = () => {
        if(isHost()){
            console.log("start game");
            setTimer(TIME_PHASE_CARDS, true);
            const randomPlayer = randInt(0, players.length - 1); // random for start first
            setPlayerStart(randomPlayer, true);
            setPlayerTurn(randomPlayer, true);
            setRound(1, true);
            setDeck([
                ...new Array(16).fill(0).map(() => "punch"),
                ...new Array(24).fill(0).map(() => "grab"),
                ...new Array(8).fill(0).map(() => "shield"),
            ],
            true
            );
            setGems(NB_GEMS, true)
            setCurrentEmoji([], true)
            players.forEach(player => {
                console.log("Setting up player", player.id);
                player.setState("cards", [], true);
                player.setState("gems", 0, true);
                player.setState("sheield", false, true);
            });
            distributeCards(CARDS_PER_PLAYER);
            setPhase("cards", true)
        }
    };



    // const setNewEmoji = (emoji,id) => {
    //     setCurrentEmoji([...currentEmoji, {emoji: emoji, id: id}], true)
    // }
    const getEmoji = () => {
        return getState("emoji");
    };

    useEffect(() => {
        startGame();
        onPlayerJoin(startGame);
    }, []);

    const { paused } = useControls({
        paused: false,
    });

    return (
        <GameEngineContext.Provider value={{...gameState, startGame}}>
            {children}
        </GameEngineContext.Provider>
    );
};

export function setNewEmoji(emoji, id) {
    setCurrentEmoji([...currentEmoji, {emoji: emoji, id: id}])
    return
};

export const useGameEngine= () => {
    const context = React.useContext(GameEngineContext);
    if (context===undefined){
        throw new Error("useGameEngine must be use within a GameEngineProvider")
    }
    return context
};
